<template>
    <div id="translateForm">
        <div class="wrapper">
            <form class="form-inline well" @submit="formSubmit">
                <input class="form-control" type="text" v-model="textToTranslate" placeholder="Enter a word">
                <select class="form-control" v-model="language">
                    <option value="ru">Russian</option>
                    <option value="sr">Serbian</option>
                    <option value="zh">Chinese</option>
                    <option value="lt">Lithuanian</option>
                    <option value="ko">Korean</option>
                    <option value="de">German</option>
                    <option value="hi">Hindi</option>
                    <option value="pt">Portuguese</option>
                </select>
                <input class="btn btn-primary" type="submit" value="Translate">
            </form>
        </div>
    </div>
</template>

<script>
export default {
    name: 'translateForm',
    data () {
        return {
            textToTranslate: '',
            language: ''
        }
    },
    created () {
        this.language = 'sr'
    },
    methods: {
        formSubmit (e) {
            this.$emit('formSubmit', this.textToTranslate, this.language)
            e.preventDefault()
        }
    }
}
</script>

<style>

.wrapper {max-width: 600px; margin: 0 auto; display: flex; justify-content: space-around}
#translateForm {padding: 50px 0;}
select {margin: 0 20px;}

</style>



