<template>
  <div class="text-center" id="app">
    <h1>Word Translator</h1>
    <h5>Powered by Vue.js</h5>
    <TranslateForm @formSubmit="translateText"></TranslateForm>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'App',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data () {
    return {
      translatedText: ''
    }
  },
  methods: {
    translateText (text, lang) {
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180615T073925Z.3266ba64d68f2b02.b0f08d00fc92e49f40e47724a90c22ea3f5c7629&lang=' + lang + '&text=' + text)
        .then(response => {
          this.translatedText = response.body.text[0]
        })
    }
  }
}
</script>

<style>

#app {padding-top: 100px;}
body {background-color: #fefefe;}

</style>



